body {
  padding: 0;
  margin: 0;
}
nav {
  display: flex;
  justify-content: space-around;
  padding: auto 5rem;
  border: 1px solid black;
}

.logo {
  height: 100px;
}

nav > .nav-buttons {
  display: flex;
  list-style-type: none;
  align-items: center;
}

nav > .nav-buttons > li {
  padding: 0 2rem;
}

.search-container {
  text-align: center;
}

.shimmer-card {
  background-color: grey;
  opacity: 0.1;
  height: 20rem;
  animation-name: shimmer-bg;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
}

@keyframes shimmer-bg {
  0% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.1;
  }
}

.restaurant-card {
  border: 1px solid black;
  border-radius: 25px;
  margin: 1rem;
  padding: 1rem;
  width: 15rem;
}

.restaurant-card > img {
  width: 100%;
  object-fit: cover;
}
.restaurant-card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.restaurant-card-details > h5 {
  overflow-wrap: break-word;
}
.menu-card {
  border: 1px solid black;
  border-radius: 25px;
  margin: 1rem;
  padding: 1rem;
  width: 15rem;
  text-align: center;
}
.restaurant-details {
  text-align: center;
}
.menu-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 2.5rem;
}
.center {
  display: flex;
  justify-content: center;
}
